import styled from "styled-components";
import { useSelector, useDispatch } from 'react-redux';
import { loginUser } from '../../../store/store';
import React, { useState, useEffect } from 'react';
import axios from 'axios'
import { useNavigate } from "react-router-dom";
import Input from "./ui/Input";
import Button from "./ui/Button";
import A from "./ui/A";
import DatePickerUI from "./ui/DatePickerUI";

const LogoImage = styled.img`
  width: auto;
  height: 200px;
`

const SignupContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 0 20px;
`;

const SignupForm = styled.form`
  display: flex;
  flex-direction: column;
`;

const Infotitle = styled.div`
  font-weight: 400;
  text-align: left;
`

const GenderSelect = styled.select`
  font-size: 16px;
  font-family: 'Noto-Sans KR', 'sans-serif';
  padding: 12px;
  border: 1px solid black;
  border-radius: 4px;
`

const GenderOption = styled.option`
  font-size: 15px;
  font-family: 'Noto-Sans KR', 'sans-serif';
`

function SignupPage() {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('')
  const [name, setName] = useState('');;
  const [PasswordCheck, setPasswordCheck] = useState('');
  const [birth, setBirth] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('')
  const [gender, setGender] = useState('');
  const [connection, setConnection] = useState('');

  const checkEmailExists = async (email) => {
     return new Promise((resolve) => {
       setTimeout(() => {
         //       
         //  false     
         resolve(false);
       }, 1000);
    });
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    // console.log(birth, typeof birth);
    // const birthday = String(birth).split(" ");
    // console.log(birthday); // 3: year 2: day 1: month 0: 
    //   
    if (!name || !email || !password || !PasswordCheck || !birth || !phoneNumber || !gender){
      alert('  .');
      return;
    }
    //    
    if (password !== PasswordCheck) {
      alert('  .');
      return;
    }
    //   
    const emailExists = await checkEmailExists(email);
    if (emailExists){
      alert('  .    .');
      return;
    }
    //  
    const nameRegex = /^[a-zA-Z-]/;
    if (!nameRegex.test(name)){
      alert('   .');
      return;
    }
    //   
    const phoneRegex = /01[016789]-[^0][0-9]{2,3}-[0-9]{3,4}/;
    if (!phoneRegex.test(phoneNumber)){
      alert('    : 010-1010-1010');
      return;
    }

    //     
    const body = { name, email, password, birth, phoneNumber, gender }
    axios.post('http:/44.213.59.255:4000/api/auth/signup', body)
        .then(response => {
          //    
          navigate('/')
        })
        .catch(error => {
          //   
          console.error(error);
          alert("   .")
        });
  };

  return (
      <SignupContainer>
        <LogoImage src="../../images/youtube.png" alt="signupImage"/>
        <h1></h1>
        <SignupForm onSubmit={handleSubmit}>
          <Infotitle></Infotitle>
          <Input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder=" "
          />
          <Infotitle></Infotitle>
          <Input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder=" "
          />
          <Infotitle> </Infotitle>
          <Input
              type="password"
              value={PasswordCheck}
              onChange={(e) => setPasswordCheck(e.target.value)}
              placeholder=" "
          />
          <Infotitle></Infotitle>
          <Input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder=" "
          />
          <Infotitle> </Infotitle>
          <Input
              type="text"
              value={phoneNumber}
              onChange={(e)=>{setPhoneNumber(e.target.value)}}
              placeholder=" "
          />
          <Infotitle></Infotitle>
          <Input
              type="text"
              value={birth}
              onChange={(e)=>{setBirth(e.target.value)}}
              placeholder=" "
          />
          <Infotitle></Infotitle>
          <GenderSelect value={gender} onChange={(e) => setGender(e.target.value)}>
            <GenderOption> </GenderOption>
            <GenderOption value=""></GenderOption>
            <GenderOption value=""></GenderOption>
          </GenderSelect>

          <Button type="submit"></Button>
        </SignupForm>
        <p>
            ? <A href="/" content="" />
        </p>
      </SignupContainer>
  );
}

export default SignupPage;
